.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  margin-top: 105px;
}

.logo-title {
  font-family: "EB Garamond", sans-serif;
  font-weight: 700;
  font-size: 2rem;
}
.navbar {
  box-shadow: 2px 2px 5px #3292a6;
  opacity: 0.9;
  background: #fff;
}
.navbar .nav-item {
  font-size: 1.4rem;
  padding-right: 1.4rem;
}
.nav-link {
  font-family: "Spartan", sans-serif;
  font-weight: 700;
}
.logo-img {
  margin-right: 10px;
}
.slogan {
  font-family: "Bellota Text";
  color: #ddd;
}

.sub-slogan {
  font-family: "Bellota Text";
  font-size: 2rem;
  color: #eee;
}
.main-img {
  position: absolute;
  top: 50%;
  transform: translate(0, -30%);
}
#showcase {
  position: relative;
  background: url("./img/main.jpg");
  background-size: cover;
  background-position: center;
  min-height: 600px;
}
#showcase .primary-overlay {
  background: rgba(66, 66, 66, 0.8);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
#showcase1 {
  position: relative;
  background: url("./img/main.jpg");
  min-height: 665px;
}
#showcase1 .primary-overlay {
  background: rgba(50, 146, 166, 0.8);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.info-header {
  width: 50%;
  margin: auto;
  border-bottom: 1px #ddd solid;
}

#authors img {
  margin-top: -50px;
}
#authors .card:hover {
  background: #3292a6;
  color: #fff;
}
#authors .fab {
  color: #fff;
}
.aboutSize {
  font-size: 1.25rem;
}
@media (max-width: 768px) {
  #showcase {
    min-height: 500px;
  }
  #showcase h1 {
    font-size: 4rem;
  }
  .mb-resp {
    margin-bottom: 1rem;
  }
}
